import hexSha1 from 'hex-sha1';
import mSearch from '@/components/m-search/m-search.vue';
import uniLoadMore from "@/components/uni-load-more.vue";
import {
	ChangePageTitle
} from '../../../title.js'
export default {
	data() {
		return {
			title: '客户列表',
			back1: true,
			// accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
			// accessToken:'ff4188b86eaa05bf676824101c24d7db28f4c435',
			// appsercert: 'MTExMTEx',
			// appsercert:'c2VsZmltcHJfd3cxMTVmZDA2ZDU1ZjJiZjUx',
			accessToken:'',
			appsercert:'',
			sha: '',
			list: [],
			page: 1,
			status: 'more',
			isShow: false,
			show:false,
		}
	},
	computed: {
		Screen_width() {
			return uni.getSystemInfoSync().windowWidth;
		}
	},
	components: {
		mSearch,
		uniLoadMore
	},
	methods: {
		getinfo() {
			var signStr =
				"access-token=" +
				this.accessToken +
				"&appsercert=" +
				this.appsercert +
				"&page=" +
				this.page;
			// console.log(signStr)
			this.sha = hexSha1(signStr);
			console.log(this.sha)
			this.$axios(this.$baseUrl.customlist, {
					'access-token': this.accessToken,
					sign: this.sha,
					page: this.page
				})
				.then(res => {
					console.log(res)
					uni.hideLoading()
					if(res.data.items.length == 0){
						this.show = true
					}else{
						this.show = false
					}
					if (res.data.items.length == 0) {
						this.status = "noMore";
					} else {
						console.log(res.data);
						this.list = this.list.concat(res.data.items);
						for (var i = 0; i < this.list.length; i++) {
							this.$set(this.list[i], 'slide_x', 0)
						}
						this.isShow = true
						// console.log(this.list)
						alert(JSON.stringify(this.list))
					}
				}, (error) => {
					console.log(error)
				})
		},
		getDetail() {
			console.log('详情')
		},
		// 滑动开始
		touchStart(e, index) {
			//记录手指放上去的时间
			this.startTime = e.timeStamp;
			//记录滑块的初始位置
			this.start_slide_x = this.list[index].slide_x;
			// 按钮宽度
			uni.createSelectorQuery()
				.selectAll('.group-btn')
				.boundingClientRect()
				.exec(res => {
					if (res[0] != null) {
						this.btnWidth = res[0][index].width * -1;
					}
				});
			// 记录上一次开始时手指所处位置
			this.startX = e.touches[0].pageX;
			// 记录上一次手指位置
			this.lastX = this.startX;
			//初始化非当前滑动消息列的位置
			this.list.forEach((item, eq) => {
				if (eq !== index) {
					item.slide_x = 0;
				}
			});
		},
		// 滑动中
		touchMove(e, index) {
			const endX = e.touches[0].pageX;
			const distance = endX - this.lastX;
			// 预测滑块所处位置
			const duang = this.list[index].slide_x + distance;
			// 如果在可行区域内
			if (duang <= 0 && duang >= this.btnWidth) {
				this.list[index].slide_x = duang;
			}
			// 此处手指所处位置将成为下次手指移动时的上一次位置
			this.lastX = endX;
		},
		// 滑动结束
		touchEnd(e, index) {
			let distance = 10;
			const endTime = e.timeStamp;
			const x_end_distance = this.startX - this.lastX;
			if (Math.abs(endTime - this.startTime) > 200) {
				distance = this.btnWidth / -2;
			}
			// 判断手指最终位置与手指开始位置的位置差距
			if (x_end_distance > distance) {
				this.list[index].slide_x = this.btnWidth;
			} else if (x_end_distance < distance * -1) {
				this.list[index].slide_x = 0;
			} else {
				this.list[index].slide_x = this.start_slide_x;
			}
		},
		// 点击回复原状
		recover(index) {
			this.list[index].slide_x = 0;
		},
		// 删除
		removeM(id) {
			let self = this
			console.log('点击删除')
			uni.showModal({
				title: '',
				content: '确定要删除该信息吗？',
				confirmText: '删除',
				confirmColor: '#ff3b32',
				success: function(res) {
					if (res.confirm) {
						var signStr = "access-token=" + self.accessToken + "&t_uid=" + id;
						let sha = hexSha1(signStr);
						uni.request({
							url: "/v2/sale/sale/delete?id=" +
								id +
								"&access-token=" +
								self.accessToken +
								"&sign=" +
								sha +
								"",
							method: 'DELETE',
							success: (res) => {
								console.log(res)
								alert(JSON.stringify(res))
								if (res.data.status) {
									uni.showToast({
										title: res.data.msg,
										icon: 'success'
									})
									this.getinfo()
								} else {
									uni.showToast({
										title: res.data.msg,
										icon: 'none'
									})
								}
							}

						})
					} else if (res.cancel) {
						console.log('用户点击取消')
					}
				}
			});
		},
		onReachBottom() {
			this.status = "loading";
			console.log("我被拉到底部了");
			setTimeout(() => {
				this.page++;
				console.log(this.page);
				this.getinfo();
			}, 2000)
		},
		//客户详情
		godetail(id) {
			uni.navigateTo({
				url: '/pages/admindetail/index?id=' + id
			})
		}
	},

	created() {
		var list =  uni.getStorageSync('login')
		if(list){
			// alert(JSON.stringify(list))
			this.accessToken = list.access_token
			this.appsercert = list.appsercert
			// alert(this.appsercert)
			// alert(this.accessToken)
		}
		uni.showLoading({
			title: '加载中'
		})
		this.getinfo()
	},
	onShow() {
		document.title = '客户列表'
		ChangePageTitle('客户列表')
	}
}
